//---------------------------------------------- 
//  Family: Max and Max Mono
//  Weights: 400=reg, 700=bold
//  Base64 embedding for out font-face.
//  Convert ttf/otf to woff, then base64 that shit.
//----------------------------------------------

// Regular
@font-face {
  font-family: "maax";
  src: url(data:application/x-font-woff;charset=utf-8;base64,);
  font-style: normal;
  font-weight: 400;
}

// Italic
@font-face {
  font-family: "maax";
  src: url(data:application/x-font-woff;charset=utf-8;base64,);
  font-style: italic;
  font-weight: 400;
}

// Medium
@font-face {
  font-family: "maax";
  src: url(data:application/x-font-woff;charset=utf-8;base64,);
  font-style: normal;
  font-weight: 600;
}

// Bold
@font-face {
  font-family: "maax";
  src: url(data:application/x-font-woff;charset=utf-8;base64,);
  font-style: normal;
  font-weight: 700;
}


// mono
@font-face {
  font-family: "maax-mono";
  src: url(data:application/x-font-woff;charset=utf-8;base64,);
  font-style: normal;
  font-weight: 400;
}
